<template>
  <div class="to-do">
    <div class="to-do-title">
      <h2>{{title}}</h2>
      <span>100</span>
    </div>
    <ul class="to-do-list">
      <li v-for="(item,index) in 10" :key="index">
        <p>
          <strong>●</strong>图解：福建省进一步支持全省中小企业发展十条措施...
        </p>
        <span>2019-02-22 10:22:10</span>
      </li>
    </ul>
    <div class="to-do-pagination">
      <el-pagination
        background
        layout="prev, pager, next, jumper"
        prev-text="上一页"
        next-text="下一页"
        :total="1000"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
      ></el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    title: String
  },
  data() {
    return {
      currentPage: 1
    };
  },
  methods: {
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    }
  }
};
</script>
<style less="scss" scoped>
.to-do {
  background: #fff;
  margin: 10px;
  box-sizing: border-box;
}
.to-do-title {
  height: 52px;
  display: flex;
  align-items: center;
  padding-left: 20px;
  border-bottom: 1px solid #d5d5d5;
  font-size: 16px;
  box-sizing: border-box;
}
.to-do-title span {
  background: #0270c1;
  color: #fff;
  height: 16px;
  line-height: 16px;
  padding: 0 3px;
  margin-left: 5px;
  border-radius: 4px 4px 4px 0;
  box-sizing: border-box;
  font-size: 12px;
}
.to-do-list {
  padding: 20px 40px;
  box-sizing: border-box;
}
.to-do-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #343434;
}
.to-do-list li strong {
  margin-right: 6px;
  font-size: 10px;
}
.to-do-list li span {
  color: #555555;
}
.to-do-pagination {
  display: flex;
  justify-content: center;
  padding-bottom: 50px;
}
</style>
<style>
.el-pagination.is-background .btn-next,
.el-pagination.is-background .btn-prev {
  border: 0.5px solid #999999;
  padding: 0 4px;
}
</style>


